    <div v-else>
      <b-row>
        <b-col cols="11" xl="12">
          <b-row id="graph-area">
            <b-col class="graph-area">
              <BarGraph
                v-if="costInfoDataObject.length > 0"
                :key="componentKey"
                :monthly-data="costInfoDataObject"
                graph-view-type="monthly"
              />
              <div
                v-else
                class="graph-area d-flex justify-content-center align-items-center text-danger"
              >
                {{ $t("GENRALDATAERRORMESSAGE") }}
              </div>
            </b-col> </b-row
          ><br /> </b-col
      ></b-row>
      <b-row>
        <b-col
          class="text-right d-none d-lg-flex d-md-none justify-content-end align-items-center"
          cols="12"
          xl="7"
          lg="7"
        >
          <b-button class="mntbtn">{{ $t("MONTHLYCOMPARISON") }}</b-button>
          <b-button @click="dailyView" class="mnt2btn">{{
            $t("YEARCOMPARISON")
          }}</b-button>
        </b-col>
        <b-col cols="12" xl="2" lg="2"></b-col>
        <b-col
          class="d-flex justify-content-end float-right mblAlign"
          cols="12"
          xl="3"
          lg="3"
        >
          <div
            class="py-2 px-2 pillButtons bg-white d-none d-lg-flex d-md-none justify-content-center align-items-center"
          >
            <b-link
              @click.prevent="onPreviousClick"
              href="javascript:;"
              class="link d-flex justify-content-center align-items-center"
            >
              <span class="">
                <i
                  class="fa fa-caret-left fa-2x text-muted mr-2"
                  aria-hidden="true"
                ></i>
              </span>
              <span class="mr-2">{{ $t("PREVIOUSYEAR") }}</span>
            </b-link>
            &nbsp;|
            <b-link
              @click="onNextClick"
              href="javascript:;"
              class="link ml-2 d-flex justify-content-center align-items-center"
            >
              <span>{{ $t("FOLLOWINGYEAR") }}</span>
              <span class="">
                <i
                  class="fa fa-caret-right fa-2x text-muted ml-2"
                  aria-hidden="true"
                ></i>
              </span>
            </b-link>
          </div>
        </b-col>
        <b-col
          class="d-flex d-lg-none justify-content-end float-right mblAlign"
          cols="12"
          xl="3"
          md="12"
        >
          <div
            class="py-2 px-2 pillButtons bg-white d-flex justify-content-center align-items-center"
          >
            <b-link
              @click="onPreviousClick"
              href="javascript:;"
              class="link d-flex justify-content-center align-items-center"
            >
              <span class="">
                <i
                  class="fa fa-caret-left fa-2x text-muted mr-2"
                  aria-hidden="true"
                ></i>
              </span>
              <span class="mr-2">{{ $t("PREVIOUSYEAR") }}</span>
            </b-link>
            &nbsp;|
            <b-link
              @click="onNextClick"
              href="javascript:;"
              class="link ml-2 d-flex justify-content-center align-items-center"
            >
              <span>{{ $t("FOLLOWINGYEAR") }}</span>
              <span class="">
                <i
                  class="fa fa-caret-right fa-2x text-muted ml-2"
                  aria-hidden="true"
                ></i>
              </span>
            </b-link>
          </div>
        </b-col>
      </b-row>
      <br />
      <br />
      <b-container class="bv-example-row">
        <b-row cols="12" cols-sm="2" cols-md="4" cols-lg="6">
          <b-col class="formatLine mb-2 text-center fixedWidth"
            ><div class="small-fs mt-1 headerColor">
              {{ $t("INDUSTRYAVERAGEAMOUNT") }}
            </div>
            <div class="mt-2">
              <span v-if="expensisItemsYearDataObject.cost_industry_average">
                {{
                  expensisItemsYearDataObject.cost_industry_average
                    | currencyFilter
                }}{{ $t("YEN") }} </span
              ><span v-else>-</span>
            </div></b-col
          >
          <b-col class="formatLine mb-2 text-center fixedWidth"
            ><div class="small-fs mt-1 headerColor adjustHeader">
              {{ $t("SAMESCALEEVALUATION") }}{{ $t("SALES") }}
            </div>
            <div class="mt-1">
              <img
                v-if="clientJudgInfoYObject.judg_same_size_revenue"
                :src="
                  getImage(
                    judgmentIcon[
                      clientJudgInfoYObject.judg_same_size_revenue - 1
                    ]
                  )
                "
                class="text-muted"
                aria-hidden="true"
                width="45%"
                alt="icon"
              />
              <span v-else>-</span>
            </div></b-col
          >
          <b-col class="formatLine mb-2 text-center fixedWidth"
            ><div class="small-fs mt-1 headerColor adjustHeader">
              {{ $t("SAMESCALEEVALUATION") }}{{ $t("NOOFEMPLOYEE") }}
            </div>
            <div class="mt-1 mb-1">
              <img
                v-if="clientJudgInfoYObject.judg_same_size_employee_number"
                :src="
                  getImage(
                    judgmentIcon[
                      clientJudgInfoYObject.judg_same_size_employee_number - 1
                    ]
                  )
                "
                class="text-muted"
                aria-hidden="true"
                width="45%"
                alt="icon"
              />
              <span v-else>-</span>
            </div></b-col
          >
          <b-col class="formatLine mb-2 text-center fixedWidth"
            ><div class="small-fs mt-1 headerColor adjustHeader">
              {{ $t("SAMESCALEEVALUATION") }}{{ $t("SUBO") }}
            </div>
            <div class="mt-1 mb-1">
              <img
                v-if="clientJudgInfoYObject.judg_same_size_floor_space"
                :src="
                  getImage(
                    judgmentIcon[
                      clientJudgInfoYObject.judg_same_size_floor_space - 1
                    ]
                  )
                "
                class="text-muted"
                aria-hidden="true"
                width="45%"
                alt="icon"
              />
              <span v-else>-</span>
            </div></b-col
          >
          <b-col class="formatLine mb-2 text-center fixedWidth"
            ><div class="small-fs mt-1 headerColor">
              {{ $t("OVERALLEVALUATIONYEAR") }}{{ $t("YEARPARENTHESIS") }}
            </div>
            <div class="mt-1">
              <span
                v-if="comprehensiveEvaluationPt[clientJudgInfoYObject.judg - 1]"
                >{{ comprehensiveEvaluationPt[clientJudgInfoYObject.judg - 1] }}
              </span>
              <span v-else>-</span>
            </div></b-col
          >
          <b-col class="formatLine mb-2 text-center fixedWidth"
            ><div class="small-fs mt-1 headerColor adjustHeader">
              {{ $t("AMOUNTOFREDUCTIONYEAR") }} {{ $t("ANNUALAMOUNT") }}
            </div>
            <div class="mt-1">
              <span
                v-if="clientJudgInfoYObject.cost_reduction_range"
                class="amountDisplay"
              >
                {{ $t("GLOBALYEN") }}
                {{ clientJudgInfoYObject.cost_reduction_range | currencyFilter
                }}{{ $t("YEN") }}
              </span>
              <span v-else class="pt-2">-</span>
            </div></b-col
          >
          <b-col class="text-center mb-2 fixedWidth"
            ><div class="small-fs mt-1 headerColor">
              {{ $t("REDUCTIONPLAN") }}
            </div>
            <div class="mt-1 mb-3 adjustHeader">
              <span
                id="proposal-logo"
                v-if="expenseData.reductionProposalStatus == true"
                class="icon-background"
              >
                <img
                  src="@/assets/plan-icon.png"
                  class="text-muted reduceSpace"
                  aria-hidden="true"
                  alt="icon"
                /><b-link
                  @click="expensePreview"
                  href="javascript:;"
                  class="card-link link graph-selected proposal-fs text-color"
                >
                  {{ $t("VIEWREDUCTIONPLAN") }}
                </b-link>
