    </b-row>
  </div>
</template>

<script>
import BarGraph from "@/components/graphs/BarGraph.vue";
// import SubMenuTree from "@/components/SubMenuTree.vue";
import api from "@/shared/api";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { Settings } from "luxon";
import { mapGetters } from "vuex";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";
import { findImage } from "@/shared/utils";

export default {
  name: "MonthlyExpenseDetails",
  components: { BarGraph },
  data() {
    return {
      comprehensiveEvaluationPt: [
        this.$t("OVERALLJUDGEMENTCHART.SLIM"),
        this.$t("OVERALLJUDGEMENTCHART.HEALTHY"),
        this.$t("OVERALLJUDGEMENTCHART.FAT"),
        this.$t("OVERALLJUDGEMENTCHART.METABO"),
        this.$t("OVERALLJUDGEMENTCHART.SPOILER"),
      ],
      loading: false,
      componentKey: 0,
      expenseData: [],
      subTreeData: [],
      treeAllData: [],
      selected_expense: {},
      expenseOptions: [],
      selectExpenseItem: this.$t("EXPENSEITEMSELECTION"),
      selectedExpenseItem: "",
      costInfoDataObject: [],
      costInfoDataObjectProps: [],
      clientJudgInfoYObject: {},

      clientJudgInfoYObjectCopy: {
        judg_year: "",
        accounts_name_cd: "",
        accounts_name_nm: "",
        expense_item_cd: "",
        expense_item_nm: "",
        reduction_range: "", //17
        judg_reduction_range: "",
        judg_sense_of_cost: "",
        judg: "", //16
        judg_same_size_revenue: "", //13
        judg_same_size_employee_number: "", //14
        judg_same_size_floor_space: "", //15
      },
      expensisItemsYearDataObject: {
        cost_industry_average: "", //12
      },

      judgmentIcon: ["fa-sun-o", "fa-cloud", "fa-umbrella"],
    };
  },
  computed: {
    ...mapGetters({
      expenseItemCode: "getExpenseItemCode",
      userAccountData: "getUserDetails",
      selected_expense_item: "getSelectedExpenseItem",
      expenseItemYear: "getExpenseItemYear",
    }),
  },
  created() {
    this.getTreeData();
  },
  mounted() {
    this.getDataExpense();
  },
  methods: {
    getImage(item) {
      return findImage(item);
    },
    onPreviousClick() {
      this.$store.dispatch("updateExpenseItemYear", {
        expense_item_year: "",
      });
      this.getDataExpense(this.expenseData.clientAvailableYear - 1);
    },
    onNextClick() {
      this.$store.dispatch("updateExpenseItemYear", {
        expense_item_year: "",
      });
      this.getDataExpense(this.expenseData.clientAvailableYear + 1);
    },
    dailyView() {
      this.$router.push({ name: "yearly_expense_details" });
    },
    updateExpenseItem(item) {
      this.selectedExpenseItem = item;
      let treeItem = this._.find(this.treeAllData, { expense_item_nm: item });
      this.$store.dispatch("updateSelectedExpenseItem", {
        accounts_name_cd: treeItem.accounts_name_cd,
        expense_item_cd: treeItem.expense_item_cd,
        expense_item_nm: treeItem.expense_item_nm,
      });
      this.getDataExpense(this.expenseData.clientAvailableYear);
    },
    returnToExpenseEvaluationList() {
      this.$router.push({ name: "expense_evaluation" });
    },
    expensePreview() {
      this.$router.push({ name: "reduction_proposal_preview" });
    },
    async getTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.EVALUATIONCOSTINFOTREELIST,
        {
          client_no: this.userAccountData.client_account_no,
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.treeAllData = responseData;

        let mapData = [];
        let groupedData = this._.groupBy(responseData, "accounts_name_nm");

        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          groupedData[key].forEach((item) => {
            let bindChildData = {
              text: item.expense_item_nm,
              expenseItemCode: item.expense_item_cd,
              accountNameCode: item.accounts_name_cd,
            };
            childrenArray.push(bindChildData);
          });
          let bindTreeData = {
            text: key,
            state: { expanded: true },
            children: childrenArray,
          };
          mapData.push(bindTreeData);
        });
        this.$store.dispatch("updateTree", mapData);
        this.displayButtons();
      }
    },
    drawGraph() {
      setTimeout(() => {
        let treeItem = this._.find(this.treeAllData, {
          expense_item_cd: this.selected_expense.expense_item_cd,
        });
        if (treeItem) {
          this.$store.dispatch("updateSelectedExpenseItem", {
            accounts_name_cd: treeItem.accounts_name_cd,
            expense_item_cd: treeItem.expense_item_cd,
            expense_item_nm: treeItem.expense_item_nm,
          });
        }
        this.getDataExpense(this.expenseData.clientAvailableYear);
      }, 0);
    },
    displayButtons() {
      this.treeAllData.forEach((item) => {
        let iterateExpenseItem = {
          text: item.expense_item_nm ? item.expense_item_nm : "",
          value: {
            expense_item_cd: item.expense_item_cd ? item.expense_item_cd : "",
            accounts_name_cd: item.accounts_name_cd
              ? item.accounts_name_cd
              : "",
          },
        };
        if (item.expense_item_nm) {
          this.expenseOptions.push(iterateExpenseItem);
        }
      });
      if (this.selected_expense_item.expense_item_nm) {
        this.selected_expense = {
          accounts_name_cd: this.selected_expense_item.accounts_name_cd,
          expense_item_cd: this.selected_expense_item.expense_item_cd,
        };
      }
    },
    async getDataExpense(year = "") {
      this.loading = true;
      let { data } = await api.post(endPoint.EVALUATIONREADEXPENSISYEAR, {
        client_no: this.userAccountData.client_account_no,
        accounts_name_cd: this.selected_expense_item.accounts_name_cd,
        expense_item_cd: this.selected_expense_item.expense_item_cd,
        year: this.expenseItemYear.expense_item_year
          ? this.expenseItemYear.expense_item_year
          : year,
      });
      this.loading = false;
      this.selectedExpenseItem = this.selected_expense_item.expense_item_nm;
      if (data.success) {
        this.expenseData = data.data;
        this.expenseData.clientAvailableYear = parseInt(
          data.data.clientAvailableYear
        );
        if (this.expenseData.costInfoDataObject.length > 0) {
          this.clientJudgInfoYObject = this.expenseData.clientJudgInfoYObject
            ? this.expenseData.clientJudgInfoYObject
            : this.clientJudgInfoYObject;
          this.costInfoDataObject = this.expenseData.costInfoDataObject.map(
            (data) => {
              return {
                xKey: parseFloat(String(data.cost_monthly).slice(4, 6)),
                value: parseFloat(data.cost),
              };
            }
          );
          this.costInfoDataObject = this._.sortBy(
            this.costInfoDataObject,
            "xKey"
          );

          this.expensisItemsYearDataObject = this.expenseData
            .expensisItemsYearDataObject[0]
            ? this.expenseData.expensisItemsYearDataObject[0]
            : 0;
        }

        if (this.expenseData.costInfoDataObject.length <= 0) {
          this.costInfoDataObject = this.costInfoDataObjectProps;
          this.clientJudgInfoYObject = this.clientJudgInfoYObjectCopy;
          this.loading = false;
          this.expensisItemsYearDataObject = "";
          this.componentKey = this.componentKey + 1;
        }
      } else {
        this.costInfoDataObject = this.costInfoDataObjectProps;
        this.clientJudgInfoYObject = this.clientJudgInfoYObjectCopy;
        this.loading = false;
        this.expensisItemsYearDataObject = "";
        this.expenseData.reductionProposalStatus = false;
        this.expenseData.clientAvailableYear = new Date().getFullYear();
        this.componentKey = this.componentKey + 1;
      }
    },
  },
};
</script>

