<style lang="scss">
$primary: #062854 !default;
$shadow-color: #dbd6d6;
@media only screen and (max-width: 992px) {
  .fixedWidth {
    min-width: 50%;
  }
}

$primary: #062854 !default;
$shadow-color: #dbd6d6;

#radio-group-expense .custom-control-label {
  border: 0.125rem solid #ffffff;
  border-radius: 3px;
  margin: 4px;
  padding: 0px 8px 0px 30px;
  min-width: 100px;
  box-shadow: 0 0 10px $shadow-color;
}
#radio-group-expense .custom-control-label::before {
  border-radius: 50% !important;
}

